function slider ({container, slide, nextArrow, prevArrow, totalCounter, currentCaunter, wrapper, field}) {
  //Slider (вариант сложнее - карусель). Для этого в верстку нужно добавить доп. обертку для слайдов "offer__slider-inner"

  const slides = document.querySelectorAll(slide),
    slider = document.querySelector(container), // вводим для установки position: relative блоку (навигация по слайдеру)
    prev = document.querySelector(prevArrow),
    next = document.querySelector(nextArrow),
    total = document.querySelector(totalCounter),
    current = document.querySelector(currentCaunter),
    slidesWrapper = document.querySelector(wrapper),
    slidesField = document.querySelector(field),
    width = window.getComputedStyle(slidesWrapper).width;

  let slideIndex = 1;
  let offset = 0; // эта переменная нужна, чтобы понимать насколько мы отступили вправо или влево при помощи transform

  if (slides.length < 10) {
    total.textContent = `0${slides.length}`;
    current.textContent = `0${slideIndex}`;
  } else {
    total.textContent = slides.length;
    current.textContent = slideIndex;
  }

  slidesField.style.width = 100 * slides.length + "%";
  slidesField.style.display = "flex";
  slidesField.style.transition = "0.5s all";

  slidesWrapper.style.overflow = "hidden"; // скрываем все слайды, которые не попадают в окно slidesWrapper

  slides.forEach((slide) => {
    slide.style.width = width; // теперь мы уверены, что все слайды у нас будут одинаковой ширины
  });

  //создаем навигацию слайдера по точкам
  slider.style.position = "relative";
  const indicators = document.createElement("ol"),
    dots = [];

  indicators.classList.add("carousel-indicators");

  slider.append(indicators);

  for (let i = 0; i < slides.length; i++) {
    const dot = document.createElement("li");
    dot.setAttribute("data-slide-to", i + 1);
    dot.classList.add("dot");
    indicators.append(dot);

    if (i == 0) {
      dot.style.opacity = 1;
    }

    dots.push(dot);
  }

  function deleteNotDigits(str) {
    return +str.replace(/\D/g, ""); //убираем не цифры
  }

  next.addEventListener("click", () => {
    // //превращаем width в число и с помощью slise отрезаем px
    // if (offset == +width.slice(0, width.length - 2) * (slides.length - 1)) {
    //так же отрезаем px, только с помощью регулярки
    if (offset == deleteNotDigits(width) * (slides.length - 1)) {
      offset = 0;
    } else {
      // offset += +width.slice(0, width.length - 2);
      offset += deleteNotDigits(width);
    }
    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slideIndex == slides.length) {
      slideIndex = 1;
    } else {
      slideIndex++;
    }

    getZeroNumSlide(slides, current, slideIndex);
    dotActive(dots);
  });

  prev.addEventListener("click", () => {
    if (offset == 0) {
      // offset = +width.slice(0, width.length - 2) * (slides.length - 1);
      offset = deleteNotDigits(width) * (slides.length - 1);
    } else {
      // offset -= +width.slice(0, width.length - 2);
      offset -= deleteNotDigits(width);
    }
    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slideIndex == 1) {
      slideIndex = slides.length;
    } else {
      slideIndex--;
    }

    getZeroNumSlide(slides, current, slideIndex);
    dotActive(dots);
  });

  dots.forEach((dot) => {
    dot.addEventListener("click", (e) => {
      const slideTo = e.target.getAttribute("data-slide-to");
      slideIndex = slideTo;

      // offset = +width.slice(0, width.length - 2) * (slideTo - 1);
      offset = deleteNotDigits(width) * (slideTo - 1);
      slidesField.style.transform = `translateX(-${offset}px)`;

      getZeroNumSlide(slides, current, slideIndex);
      dotActive(dots);
    });
  });

  function getZeroNumSlide(slides, current, slideIndex) {
    if (slides.length < 10) {
      current.textContent = `0${slideIndex}`;
    } else {
      current.textContent = slideIndex;
    }
  }

  function dotActive(arr) {
    arr.forEach((dot) => (dot.style.opacity = 0.5));
    arr[slideIndex - 1].style.opacity = 1;
  }
}

export default slider;